
.out-wrapper{
  display: none;
  position: fixed;
  width: 100%;
  height: auto;
}
.m-aside{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 56px;
  overflow: hidden;
  text-align: center;
  box-shadow: 0 0.12rem .6rem 0 #e6ad76;
  background: linear-gradient(205deg, #e6ad76, #ee7b83);
}
.m-aside.full-screen{
  height: 100%;
  animation: navAnimation .666s ease forwards;
}
.circle-wrapper{
  position: fixed;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  border-radius: 2000px;
  background: #e6ad76;
}
.circle-wrapper.circle-aniamtion{
  animation: circleAnimation .666s ease forwards;
}
.m-aside header{
  position: relative;
  overflow: hidden;
}
.m-aside header .location i{
  display: inline-block;
  width: 12px;
  height: 12px;
  vertical-align: middle;
  background: url('data:image/png;base64,') no-repeat;
  background-size: 100%;
}
.m-aside .nav-menus{
  position: absolute;
  top: 0;
  left: 1rem;
  bottom: 0;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 30px;
  height: 30px;
}
.m-aside .nav-menus span{
  display: block;
  width: 100%;
  height: 5px;
  border-radius: 12px;
  background: #e6ad76;
  box-sizing: border-box;
  transition: all .32s ease;
}
.m-aside .nav-menus span:nth-child(2){
  width: 60%;
}
.m-aside .nav-menus span:nth-child(3){
  width: 90%;
}
.m-aside .nav-menus.open span{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 100%;
  background: #fff;
}
.m-aside .nav-menus.open span:first-child{
  transform: rotate(-45deg);
}
.m-aside .nav-menus.open span:last-child{
  transform: rotate(45deg);
}
.m-aside .nav-menus.open span:nth-child(2){
  width: 0;
}
.m-aside nav{
  width: 90%;
  margin: 0 auto;
  padding: 30px 0;
  border-top: 2px dashed #fff;
  border-bottom: 2px dashed #fff;
}
.m-aside nav a{
  color: #fff;
}
.m-aside nav a:hover{
    background: none;
}
.m-aside footer{
  padding: 30px 0;
}
.m-aside footer i{
  margin: 0 12px;
  color: #fff;
  font-size: 16px;
}
.m-aside h1{
  font-size: 16px;
}
.m-aside p{
  font-size: 12px;
}
.m-aside .logo{
  display: inline-block;
  width: 1.4rem;
  height: 1.4rem;
  border-width: 1px;
  vertical-align: text-bottom;
}
.m-aside .logo img{
  vertical-align: top;
}
@media screen and (max-width: 640px) {
.out-wrapper{
    display: block;
}
}
@keyframes circleAnimation{
0%{
    opacity: 1;
    top: 0;
    left: 0;
    width: 0;
    height: 0;
}
99%{
      opacity: 1;
      top: -1000px;
      left: -1000px;
      width: 2000px;
      height: 2000px;
}
100%{
    opacity: 0;
    z-index: -1;
}
}
@keyframes navAnimation{
0%{
        opacity: 0;
}
40%{
        opacity: 0;
}
100%{
        opacity: 1;
}
}



